@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

.max-h{
  height: 82vh;
}

@media (min-width: 990px){
  .max-lg-h{
      height: 82vh;
  }
}

.card{
    width: 12rem;
}
.alignmentBad{
    box-shadow: 2px 2px 10px 2px rgb(168, 25, 25);
}
.alignmentGood{
    box-shadow: 2px 2px 10px 2px rgb(25, 168, 56);
}
.cardImg{
    max-height: 220px;
}

.link{
    text-decoration: none;
    color: #fff;
}
.active{
    color: #1673FD;
}
.navbar-sticky{
    width: 100%;
    position: -webkit-sticky !important;
    position: sticky !important;
    top: 0;
    z-index: 1;
}
@media (max-width: 990px){
    .powerstats{
        position: fixed;
        bottom: 10px;
        right: 20px;
    }
}
@media (min-width: 990px){
    .powerstats{
        position: absolute;
        top: 15vh;
        right: 20px;
    }
}
